body {
  margin: 0;
  padding: 20px;
  user-select: none; }
.root {
  max-width: 1000px;
  width: 100%;
  height: 300px;
  background: #fff;
  box-sizing: border-box; }
canvas {
  background: #eee;
  width: 100%;
  height: 100%; }
.mainWrapper {
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-rows: 1fr auto auto auto; }
.canvasWrapper {
  align-self: stretch;
  position: relative;
  display: grid;
  align-self: start;
  width: 100%;
  height: 100%; }
.nameY {
  margin-top: 30px; }
.nameX {
  position: absolute;
  right: 0;
  bottom: 0; }
.slider {
  position: relative;
  display: block;
  height: 10px;
  width: 100%; }
.chunkHandle {
  position: absolute;
  left: 0;
  top: 0;
  height: 10px;
  background: #000;
  opacity: 0.5;
  cursor: pointer; }
.handle {
  position: absolute;
  left: 0;
  top: 0;
  height: 10px;
  width: 30px;
  background: #000;
  transform: translate(-50%, 0);
  opacity: 0.5;
  cursor: pointer; }
.handle:last-child {
  left: 100%; }
.tip {
  display: block;
  position: fixed;
  right: 0;
  top: 0;
  width: min-content;
  background: rgba(255,255,255,1);
  transform: translate(0, -100%); }
.chartnames {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fit, minmax(300px,1fr));
  width: 100%; }
.chartname {
  display: grid;
  grid-auto-flow: column;
  grid-gap: 5px;
  align-items: center;
  justify-content: center;
  width: auto;
  &__text {}
  &__color {
    display: block;
    width: 10px;
    height: 10px; } }

